import React, {useState} from 'react';

export default function App() {
	const questions = [
		{
			questionText: 'What do programmers miss the most?',
			answerOptions: [
				{ answerText: 'their girlfriends', isCorrect: false },
				{ answerText: '8h of sleep', isCorrect: false },
				{ answerText: 'a semicolon', isCorrect: true },
				{ answerText: 'normal eyesight', isCorrect: false },
			],
		},
		{
			questionText: 'Where did programmers learn to code?',
			answerOptions: [
				{ answerText: 'at Stanford University', isCorrect: false },
				{ answerText: 'at Harvard University', isCorrect: false },
				{ answerText: 'at MIT University', isCorrect: false },
				{ answerText: 'at Stackoverflow University', isCorrect: true },
			],
		},
		{
			questionText: 'Why do programmers keep pressing the F5 button?',
			answerOptions: [
				{ answerText: 'because it is refreshing', isCorrect: true },
				{ answerText: 'because they have to', isCorrect: false },
				{ answerText: 'to test code', isCorrect: false },
				{ answerText: 'they fell asleep', isCorrect: false },
			],
		},
		{
			questionText: 'Why should you marry a programmer?',
			answerOptions: [
				{ answerText: 'they are smart', isCorrect: false },
				{ answerText: 'they are rich', isCorrect: false },
				{ answerText: 'they are funny', isCorrect: false },
				{ answerText: 'they are not afraid to commit', isCorrect: true },
			],
		},
	];


	const [currentQuestion, setCurrentQuestion] = useState(0);
	const [showScore, setShowScore] = useState(false);
	const[score, setScore] = useState(0);

	const handleAnswerClick = (isCorrect) => {
		if(isCorrect === true){
			setScore(score+1);
		}

		const nextQuestion = currentQuestion + 1;
		if (nextQuestion<questions.length){
			setCurrentQuestion(nextQuestion);
		} else {
			setShowScore(true);
		}
		
	}

	return (
		<div className='app'>
			
			{showScore ? (
				<div className='score-section'>You scored {score} out of {questions.length}</div>
			) : (
				<>
					<div className='question-section'>
						<div className='question-count'>
							<span>Question {currentQuestion + 1}</span>/{questions.length}
						</div>
						<div className='question-text'>{questions[currentQuestion].questionText}</div>
					</div>
					<div className='answer-section'>
						{questions[currentQuestion].answerOptions.map((answerOption) => 
						<button onClick={()=> handleAnswerClick(answerOption.isCorrect)}>{answerOption.answerText}</button>)}
					</div>
				</>
			)}
		</div>
	);
}